  margin-right: 30px;
}

.item_2 {
  margin-bottom: 7px;
}

.wellcome-div {
  margin-left: 20px;
  font-family: Encode Sans;
}

.autorization {
  border-radius: 45px;
}

.sign {
  color: #fff;
  background-color: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  align-items: start;
  margin-left: 5px;
  padding: 14px 120px 15px 15px;
  font-family: DM Sans, sans-serif;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.themes {
  align-items: center;
  gap: 20px;
  margin-left: auto;
  display: flex;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.slider.round {
  border-radius: 18px;
}

.slider.round:before {
  border-radius: 50%;
}

.page-header-wrapper {
  border: 1.5px solid var(--text-secondary-color);
  background-color: var(--body-secondary-color);
  border-radius: 0 0 8px 8px;
}

.logo {
  letter-spacing: .03em;
  color: var(--second-color);
  padding-top: 24px;
  padding-bottom: 24px;
  font-size: 19px;
  font-weight: 800;
  line-height: 1.17;
}

.logo-img {
